.btnanim span {
  width: 0px;
  height: 40px;
  position: absolute;
  z-index: -1;
  left: 0px;
  bottom: 0px;
  transition-duration: 0.6s;
  transition-property: all;
}
.btnanim:hover span {
  background-color: #019875;
  width: 60%;
}
.btnanim:hover img {
  animation: ltor 0.6s ease-in-out;
  cursor: pointer;
}
@keyframes ltor {
  100% {
    transform: translateX(43px);
    width: 0px;
    opacity: 0;
  }
}
